* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  background: #faf7f5;
  color: #333;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.landing {
  width: 100%;
}

html, body {
  overflow-x: hidden;
}


.hero {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.hero-media {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}

.hero.hero-static {
  height: clamp(420px, 60vh, 760px);
}

.hero-static .hero-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}


.hero-bleed,
.video-bleed {
  margin: 0 !important;
  padding: 0 !important;
  display: block;
}




.hero-overlay{
  position: absolute;
  inset: 0;
  z-index: 1;
}

.hero-content{
  position: relative;
  z-index: 2;
}



.hero-brand {
  letter-spacing: 0.25em;
  font-size: 0.8rem;
  opacity: 0.9;
}

.hero-content h1 {
  font-size: clamp(2.4rem, 4vw, 3.8rem);
  margin: 0.6rem 0;
}

.hero-notes {
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-size: 0.9rem;
  opacity: 0.9;
}

.hero-actions {
  margin-top: 1.4rem;
  display: flex;
  gap: 0.8rem;
  flex-wrap: wrap;
}

button.ghost {
  background: transparent;
  border: 1px solid rgba(255,255,255,0.4);
  color: #fff;
  border-radius: 8px;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

button.ghost:hover {
  background: rgba(255,255,255,0.1);
}

.hero-dots {
  margin-top: 1.6rem;
  display: flex;
  gap: 0.5rem;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 999px;
  border: 1px solid rgba(255,255,255,0.65);
  background: transparent;
  cursor: pointer;
  padding: 0;
}

.dot.active {
  background: #fff;
}

.story-timeline {
  padding: 4rem 0 3rem;
}

.story-head {
  max-width: 880px;
  margin: 0 auto 2.5rem;
  text-align: center;
  padding: 0 1rem;
}

.story-kicker {
  margin: 0 0 0.35rem;
  letter-spacing: 0.18em;
  text-transform: uppercase;
  font-size: 0.78rem;
  color: #666;
}

.story-head h2 {
  margin: 0 0 0.75rem;
  font-size: clamp(1.6rem, 3vw, 2.2rem);
  letter-spacing: 0.02em;
}

.story-sub {
  margin: 0;
  color: #666;
  line-height: 1.5;
}

.timeline {
  position: relative;
  max-width: 980px;
  margin: 0 auto;
  padding: 1rem 1rem 0;
}

.timeline-line {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 2px;
  transform: translateX(-50%);
  background: rgba(184, 92, 92, 0.18);
}

.timeline-line-progress {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 2px;
  transform-origin: top;
  transform: translateX(-50%) scaleY(0);
  background: linear-gradient(
    to bottom,
    rgba(184, 92, 92, 0.0) 0%,
    rgba(184, 92, 92, 0.85) 20%,
    rgba(184, 92, 92, 0.85) 80%,
    rgba(184, 92, 92, 0.0) 100%
  );
  pointer-events: none;
}

.timeline-row {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 44px 1fr;
  align-items: start;
  margin: 1.4rem 0;

  opacity: 0;
  transform: translateY(18px);
  transition: opacity 700ms ease, transform 700ms ease;
}

.timeline-row.show {
  opacity: 1;
  transform: translateY(0);
}

.timeline-dot {
  grid-column: 2;
  justify-self: center;
  width: 14px;
  height: 14px;
  border-radius: 999px;
  background: #fff;
  border: 2px solid #b85c5c;
  box-shadow: 0 10px 22px rgba(184, 92, 92, 0.18);
  margin-top: 1.1rem;
}

.timeline-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 18px;
  padding: 1.1rem 1.1rem 1.15rem;
  box-shadow: 0 12px 26px rgba(0, 0, 0, 0.06);
}

.timeline-row.left .timeline-card {
  grid-column: 1;
}
.timeline-row.right .timeline-card {
  grid-column: 3;
}

.timeline-row.left.show {
  transform: translateY(0) translateX(-6px);
}
.timeline-row.right.show {
  transform: translateY(0) translateX(6px);
}

.timeline-top {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  margin-bottom: 0.5rem;
}

.timeline-tag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 34px;
  height: 22px;
  border-radius: 999px;
  background: #faf7f5;
  border: 1px solid #eee;
  color: #b85c5c;
  font-weight: 800;
  font-size: 0.78rem;
}

.timeline-title {
  margin: 0;
  font-size: 1.02rem;
  letter-spacing: 0.02em;
}

.timeline-body {
  margin: 0.2rem 0 0.75rem;
  color: #444;
  line-height: 1.55;
}

.timeline-more {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 450ms ease, opacity 450ms ease;
}

.timeline-more.open {
  max-height: 240px;
  opacity: 1;
}

.timeline-more p {
  margin: 0 0 0.75rem;
  color: #444;
  line-height: 1.55;
}

.timeline-toggle {
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  border: none;
  background: transparent;
  cursor: pointer;
  padding: 0;
  font-weight: 600;
  color: #b85c5c;
  letter-spacing: 0.01em;
}

.timeline-toggle:hover {
  text-decoration: underline;
}

.chev {
  display: inline-block;
  transition: transform 250ms ease;
  transform: translateY(1px);
}

.chev.up {
  transform: rotate(180deg) translateY(-1px);
}

.content {
  flex: 1;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
}

.content-details {
  flex: 1;
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem;
}

.page h1 {
  margin-bottom: 1rem;
}

.header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  background: #fff;
  border-bottom: 1px solid #eee;
}

.logo {
  font-weight: 700;
  font-size: 1.2rem;
  cursor: pointer;
}

.nav {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.nav-link {
  text-decoration: none;
  color: #555;
  font-weight: 500;
}

.nav-link.active {
  color: #b85c5c;
}

.details-button,
button.primary {
  padding: 0.5rem 0.9rem;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  background: #b85c5c;
  color: #fff;
  font-weight: 500;
}

.details-button {
  display: inline-block;
  text-decoration: none;
  text-align: center;
}

.footer {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  padding: 2.5rem 2rem 1.5rem;
  background: #1f1f1f;
  color: #f1f1f1;
}


.footer-bottom {
  grid-column: 1 / -1;
  margin-top: 2rem;
  padding-top: 1.2rem;
  border-top: 1px solid rgba(255,255,255,0.15);
  text-align: center;
  font-size: 0.85rem;
  color: rgba(255,255,255,0.75);
}

.newsletter-msg {
  margin-top: 0.5rem;
  font-size: 0.85rem;
}

.newsletter-msg[data-status="success"] {
  color: #c9f7c9;
}

.newsletter-msg[data-status="error"] {
  color: #ffb3b3;
}


.footer h4 {
  margin-top: 0;
}

.footer ul {
  list-style: none;
  padding: 0;
}

.footer a {
  color: #f1f1f1;
  text-decoration: none;
}

.footer input[type="email"] {
  width: 100%;
  margin-top: 0.25rem;
  margin-bottom: 0.5rem;
  padding: 0.4rem;
}

.contact-form,
.signin-form {
  max-width: 420px;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

label {
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
}

input,
textarea {
  margin-top: 0.25rem;
  padding: 0.4rem 0.6rem;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.products-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.perfume-card img {
  width: 100%;
  aspect-ratio: 1 / 1;
  height: auto;
  object-fit: contain;
  background: #fff;
  border-radius: 12px;
  border: 0;
  display: block;
}


.perfume-card {
  padding: 1.2rem;
  border-radius: 16px;
  background: #fff;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06);
  text-align: left;
}


.perfume-card {
  padding: 1.2rem;
  border-radius: 16px;
  background: #fff;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06);
  text-align: left;
}

.perfume-details img {
  width: 100%;
  max-width: 380px;
  height: 420px;
  object-fit: contain; 
  background: #faf7f5;
  border-radius: 14px;
  border: 0;
}


a {
  color: #b85c5c;
}

.back-link {
  display: inline-block;
  margin-top: 0.8rem;
  text-decoration: none;
  font-weight: 600;
}

.back-link:hover {
  text-decoration: underline;
}

.perfume-card:hover {
  transform: translateY(-30px);
  transition: 150ms ease;
}


.perfume-card h3 {
  margin: 0.4rem 0 0.15rem;
  font-size: 1rem;
}

.perfume-card .brand {
  margin: 0;
  font-size: 0.9rem;
  color: #777;
}

.perfume-card img {
  max-height: 250px;
  object-fit: contain;
}


.price {
  font-weight: 600;
  margin: 0.4rem 0 0.8rem;
}

.perfume-details {
  display: grid;
  grid-template-columns: 380px 1fr;
  gap: 2rem;
  align-items: start;
}

.details-info h1 {
  margin-top: 0;
  margin-bottom: 0.25rem;
}

.details-info h2 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-weight: 500;
  color: #666;
}

.details-info p {
  line-height: 1.5;
}

.details-info ul {
  padding-left: 1.2rem;
}

.account-wrapper {
  position: relative;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.profile-button {
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  padding: 0.4rem 0.8rem;
  border-radius: 999px;
  border: 1px solid #ddd;
  background: #fff;
  cursor: pointer;
  font-size: 0.9rem;
  color: #333;
}

.profile-icon {
  font-size: 1.1rem;
}

.caret {
  font-size: 0.7rem;
}

.dropdown-menu {
  position: absolute;
  right: 0;
  margin-top: 0.4rem;
  min-width: 200px;
  padding: 0.5rem 0;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.1);
  z-index: 100;
}

.dropdown-menu button {
  width: 100%;
  padding: 0.55rem 0.9rem;
  border: none;
  background: none !important;
  text-align: left;
  font-size: 0.9rem;
  cursor: pointer;
  color: #333 !important;
}

.dropdown-menu button:hover {
  background: #f5f5f5 !important;
}

.dropdown-info {
  padding: 0.55rem 0.9rem;
  font-size: 0.8rem;
  border-bottom: 1px solid #eee;
  margin-bottom: 0.3rem;
  color: #333;
}
.nav-dropdown {
  position: relative;
}

.nav-dropdown-toggle {
  border: none;
  background: none;
  cursor: pointer;
  font: inherit;
  padding: 0;
  color: #555;
  font-weight: 500;
}

.nav-dropdown-menu {
  position: absolute;
  top: 120%;
  left: 0;
  min-width: 220px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.08);
  padding: 0.35rem 0;
  z-index: 90;
}

.nav-dropdown-menu button {
  width: 100%;
  padding: 0.55rem 0.9rem;
  border: none;
  background: none !important;
  text-align: left;
  font-size: 0.9rem;
  cursor: pointer;
  color: #333 !important;
}

.nav-dropdown-menu button:hover {
  background: #f5f5f5 !important;
}

.hamburger {
  display: none;
  font-size: 1.3rem;
  border: 1px solid #ddd;
  background: #fff;
  border-radius: 10px;
  padding: 0.35rem 0.6rem;
  cursor: pointer;
}

.desktop-nav {
  display: flex;
}

.desktop-account {
  display: block;
}

.mobile-menu {
  position: absolute;
  top: 64px;
  left: 0;
  right: 0;
  background: #fff;
  border-bottom: 1px solid #eee;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.08);
  padding: 1rem;
  z-index: 200;
}

.mobile-close {
  border: 1px solid #ddd;
  background: #fff;
  border-radius: 10px;
  padding: 0.25rem 0.6rem;
  cursor: pointer;
  float: right;
}

.mobile-section {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.mobile-title {
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.mobile-muted {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 0.25rem;
}

.mobile-menu button {
  background: none !important;
  color: #333 !important;
  border: 1px solid #eee;
  border-radius: 10px;
  padding: 0.55rem 0.8rem;
  text-align: left;
}

.collections {
  background: #faf7f5;
  padding: 0 0 4rem;
}

.collections-hero {
  background: #1e1e1e;
  color: #fff;
  text-align: center;
  padding: 3.5rem 1rem 2.4rem;
}

.collections-title {
  margin: 0;
  font-size: clamp(1.8rem, 4vw, 2.6rem);
  letter-spacing: 0.01em;
}

.collections-sub {
  margin: 0.9rem 0 0.25rem;
  opacity: 0.85;
}

.collections-sub2 {
  margin: 0;
  opacity: 0.6;
  font-size: 0.95rem;
}

.collections-intro {
  max-width: 980px;
  margin: 0 auto;
  padding: 1.6rem 1rem 0.8rem;
  text-align: center;
  color: #666;
  line-height: 1.6;
}

.collections-list {
  max-width: 1100px;
  margin: 0 auto;
  padding: 1.2rem 1rem 0;
  display: grid;
  gap: 1.6rem;
}

.collection-card {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 22px;
  box-shadow: 0 18px 44px rgba(0,0,0,0.06);
  overflow: hidden;
}

.collection-card.rare-card {
  background: #1f6b2b;
  border-color: rgba(255,255,255,0.14);
  box-shadow: 0 22px 60px rgba(0,0,0,0.18);
}

.collection-inner {
  display: grid;
  grid-template-columns: 1.05fr 0.95fr;
  gap: 1.2rem;
  padding: 2rem;
  align-items: center;
}

.collection-card.reverse .collection-inner {
  grid-template-columns: 0.95fr 1.05fr;
}

.collection-card.reverse .collection-copy {
  order: 2;
}
.collection-card.reverse .collection-art {
  order: 1;
}

.collection-copy {
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
}

.collection-pill {
  display: inline-flex;
  align-self: flex-start;
  font-size: 0.72rem;
  font-weight: 800;
  letter-spacing: 0.14em;
  padding: 0.35rem 0.6rem;
  border-radius: 999px;
  border: 1px solid #eee;
  background: #f4f4f4;
  color: #333;
}

.collection-card.tone-blue .collection-pill { background: rgba(64, 147, 255, 0.12); border-color: rgba(64,147,255,0.18); }
.collection-card.tone-pink .collection-pill { background: rgba(255, 105, 180, 0.12); border-color: rgba(255,105,180,0.18); }
.collection-card.tone-purple .collection-pill { background: rgba(158, 100, 255, 0.12); border-color: rgba(158,100,255,0.18); }
.collection-card.tone-green .collection-pill { background: rgba(80, 255, 160, 0.14); border-color: rgba(80,255,160,0.18); }

.collection-h3 {
  margin: 0;
  font-size: clamp(1.6rem, 3vw, 2.2rem);
  letter-spacing: 0.01em;
  line-height: 1.05;
  color: #111;
}

.collection-card.rare-card .collection-h3,
.collection-card.rare-card .collection-desc,
.collection-card.rare-card .includes-label {
  color: rgba(255,255,255,0.92);
}

.collection-desc {
  margin: 0;
  color: #666;
  line-height: 1.65;
  max-width: 60ch;
}

.collection-card.rare-card .collection-desc {
  color: rgba(255,255,255,0.82);
}

.collection-includes {
  margin-top: 0.2rem;
}

.includes-label {
  font-size: 0.72rem;
  letter-spacing: 0.18em;
  font-weight: 800;
  color: #777;
  margin-bottom: 0.45rem;
}

.includes-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 0.45rem;
}

.mini-chip {
  display: inline-flex;
  align-items: center;
  padding: 0.35rem 0.55rem;
  border-radius: 999px;
  background: #f6f6f6;
  border: 1px solid #eee;
  font-size: 0.85rem;
  color: #333;
}

.collection-card.rare-card .mini-chip {
  background: rgba(255,255,255,0.12);
  border-color: rgba(255,255,255,0.18);
  color: #fff;
}

.collection-cta {
  margin-top: 0.25rem;
  align-self: flex-start;
  border: none;
  border-radius: 12px;
  padding: 0.7rem 1rem;
  cursor: pointer;
  font-weight: 700;
  background: #111;
  color: #fff;
}

.collection-card.rare-card .collection-cta {
  background: #fff;
  color: #111;
}

.collection-art {
  border-radius: 18px;
  height: 240px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, rgba(0,0,0,0.05), rgba(0,0,0,0.02));
  border: 1px solid #eee;
  color: rgba(0,0,0,0.25);
  font-weight: 900;
  text-align: center;
  padding: 1rem;
}

.collection-card.tone-blue .collection-art { background: linear-gradient(135deg, rgba(64,147,255,0.12), rgba(64,147,255,0.04)); }
.collection-card.tone-pink .collection-art { background: linear-gradient(135deg, rgba(255,105,180,0.12), rgba(255,105,180,0.04)); }
.collection-card.tone-purple .collection-art { background: linear-gradient(135deg, rgba(158,100,255,0.12), rgba(158,100,255,0.04)); }

.collection-card.rare-card .collection-art {
  background: rgba(255,255,255,0.12);
  border-color: rgba(255,255,255,0.18);
  color: rgba(255,255,255,0.65);
}

.collection-art-text .block {
  display: block;
  line-height: 1.12;
}

.collection-expand {
  max-height: 0;
  overflow: hidden;
  transition: max-height 420ms ease;
  background: #faf7f5;
  border-top: 1px solid #eee;
}

.collection-card.rare-card .collection-expand {
  background: rgba(255,255,255,0.06);
  border-top-color: rgba(255,255,255,0.14);
}

.collection-expand.open {
  max-height: 1200px;
}

.collection-expand-inner {
  padding: 1.2rem 1.2rem 1.4rem;
}

.collection-products {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1rem;
}

.collection-bottom {
  margin-top: 1rem;
}

.collections-quiz {
  margin-top: 2.6rem;
  background: #1e1e1e;
  color: #fff;
  text-align: center;
  padding: 3.2rem 1rem;
}

.collections-quiz h3 {
  margin: 0 0 0.65rem;
  font-size: clamp(1.3rem, 3vw, 2rem);
}

.collections-quiz p {
  margin: 0 auto 1.2rem;
  max-width: 62ch;
  opacity: 0.75;
  line-height: 1.6;
}

.quiz-btn {
  border: 1px solid rgba(255,255,255,0.25);
  background: #fff;
  color: #111;
  border-radius: 12px;
  padding: 0.75rem 1rem;
  font-weight: 800;
  cursor: pointer;
}


@media (max-width: 900px) {
  .collection-inner {
    grid-template-columns: 1fr;
    padding: 1.4rem;
  }
  .collection-card.reverse .collection-copy,
  .collection-card.reverse .collection-art {
    order: initial;
  }
  .collection-art {
    height: 200px;
  }
  .collection-products {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 560px) {
  .collection-products {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 1024px) {
  .timeline {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 640px) {
  .timeline {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
    .hero:not(.hero-static) {
    height: clamp(380px, 70vh, 560px);
    min-height: 420px;
  }

  .hero.hero-static {
       height: clamp(220px, 35vw, 340px);
  }

    .hero-static .hero-image {
    object-position: left center;
  }
  .hero-overlay {
    background: linear-gradient(
      to top,
      rgba(0,0,0,0.82) 0%,
      rgba(0,0,0,0.35) 55%,
      rgba(0,0,0,0.10) 100%
    );
  }

  .hero-content {
    max-width: 100%;
    padding: 1.25rem;
  }

  .hero-actions {
    gap: 0.6rem;
  }

    .hero-image {
    object-fit: cover;
  }
}

@media (max-width: 480px) {
  .hero {
    height: 70vh;
    min-height: 420px;
    border-radius: 16px;
  }

  .hero-overlay {
    background: linear-gradient(
      to top,
      rgba(0,0,0,0.78) 0%,
      rgba(0,0,0,0.35) 55%,
      rgba(0,0,0,0.08) 100%
    );
  }

  .video-bleed {
  margin-top: 0;
  border-top: 1px solid rgba(0,0,0,0.06);
}

  .hero-bleed,
  .video-bleed {
    width: 100%;
    left: 0;
    transform: none;
    margin: 0;
  }


  .hero-content {
    max-width: 100%;
  }
}

@media (max-width: 900px) {
  .timeline {
    grid-template-columns: 1fr 1fr;
  }

  .hero-content {
    padding: 2rem;
  }
}

@media (max-width: 600px) {
  .content {
    padding: 1rem;
  }
}


@media (max-width: 600px) {
  .hero {
    height: 75vh;
  }

  .timeline {
    grid-template-columns: 1fr;
  }

  .hero-content h1 {
    font-size: 2.2rem;
  }
}

.logo-btn {
  border: none;
  background: transparent;
  padding: 0;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}

.logo-img {
  height: 80px;
