}

@media (max-width: 820px) {
  .timeline-line,
  .timeline-line-progress {
    left: 16px;
    transform: none;
  }

  .timeline-row {
    grid-template-columns: 34px 1fr;
    gap: 0.9rem;
    margin: 1.1rem 0;
  }

  .timeline-dot {
    grid-column: 1;
    justify-self: start;
    margin-top: 1.2rem;
  }

  .timeline-row.left .timeline-card,
  .timeline-row.right .timeline-card {
    grid-column: 2;
  }

  .timeline-row.left.show,
  .timeline-row.right.show {
