.pdp-head {
  background: #fff;
  border: 1px solid #eee;
  border-radius: 22px;
  padding: 1.25rem 1.25rem 1.15rem;
  box-shadow: 0 18px 44px rgba(0,0,0,0.06);
}

.pdp-kicker {
  letter-spacing: 0.22em;
  text-transform: uppercase;
  font-size: 0.78rem;
  color: #666;
}

.pdp-title {
  margin: 0.55rem 0 0.25rem;
  font-size: clamp(2rem, 3.4vw, 2.7rem);
  line-height: 1.05;
}

.pdp-tagline {
  margin: 0 0 0.9rem;
  color: #666;
  line-height: 1.5;
}

.pdp-buyrow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  flex-wrap: wrap;
}

.pdp-price {
  display: inline-flex;
  align-items: baseline;
  gap: 0.35rem;
  font-weight: 900;
  letter-spacing: 0.01em;
}

.pdp-price .amount { font-size: 1.4rem; }
.pdp-price .cur { font-size: 1rem; color: #666; }

.pdp-stock {
  display: inline-flex;
  align-items: center;
  gap: 0.45rem;

  padding: 0.35rem 0.65rem;
  border-radius: 999px;

  background: rgba(76, 175, 80, 0.12);
  border: 1px solid rgba(76, 175, 80, 0.35);

  color: #2e7d32;
  font-weight: 700;
  font-size: 0.88rem;
}

.pdp-stock::before {
  content: "";
  width: 7px;
  height: 7px;
  border-radius: 999px;
  background: #2e7d32;
  display: inline-block;
}


.pdp-story {
  margin-top: 1rem;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 22px;
  padding: 1.1rem 1.25rem;
  box-shadow: 0 18px 44px rgba(0,0,0,0.06);
}

.pdp-desc {
  margin: 0;
  line-height: 1.75;
  color: #333;
}

.pdp-desc-wrap {
  position: relative;
  max-height: 220px;
  overflow: hidden;
  transition: max-height 500ms ease;
}

.pdp-desc-wrap::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 80px;
  background: linear-gradient(
    to bottom,
    rgba(255,255,255,0) 0%,
    #fff 75%
  );
  pointer-events: none;
}

.pdp-desc-wrap.expanded {
  max-height: 2000px;
}

.pdp-desc-wrap.expanded::after {
  display: none;
}

.pdp-readmore {
  margin-top: 0.5rem;
  border: none;
  background: none;
  padding: 0;
  cursor: pointer;

  font-weight: 700;
  font-size: 0.9rem;
  color: #b85c5c;
}

.pdp-readmore:hover {
  text-decoration: underline;
}

.pdp-origin {
  margin-top: 1rem;
  padding-top: 1rem;

  border-top: 1px solid #eee;
  text-align: left;
}

.pdp-origin p {
  margin: 0;
  font-size: 0.95rem;
  line-height: 1.6;
  color: #444;
}

.pdp-origin strong {
  font-weight: 800;
  letter-spacing: 0.01em;
}


.pdp-quote {
  margin-top: 1rem;
  background: #fff7f7;
  border: 1px solid rgba(184,92,92,0.25);
  border-left: 4px solid rgba(184,92,92,0.65);
  border-radius: 22px;
  padding: 1.05rem 1.25rem;
}

.pdp-quote-title {
  letter-spacing: 0.18em;
  text-transform: uppercase;
  font-size: 0.78rem;
  color: #8a2d2d;
  font-weight: 800;
  margin-bottom: 0.35rem;
}

.pdp-quote-text {
  margin: 0;
  line-height: 1.7;
  font-weight: 600;
  color: #333;
}

.pdp-section {
  margin-top: 1rem;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 22px;
  padding: 1.1rem 1.25rem;
  box-shadow: 0 18px 44px rgba(0,0,0,0.06);
}

.pdp-h2 {
  margin: 0 0 0.75rem;
  font-size: 1.1rem;
  letter-spacing: 0.01em;
}

.pdp-notes {
  display: grid;
  gap: 0.9rem;
}

.note-title {
  font-weight: 900;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  font-size: 0.78rem;
  color: #666;
  margin-bottom: 0.35rem;
}

.note-chips,
.pdp-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.chip {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.35rem 0.65rem;
  border-radius: 999px;
  background: #faf7f5;
  border: 1px solid #eee;
  font-size: 0.92rem;
  color: #333;
}

.pdp-stats {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 0.75rem;
}

.stat {
  background: #faf7f5;
  border: 1px solid #eee;
  border-radius: 18px;
  padding: 0.9rem;
}

.stat-ico { font-size: 1.15rem; }
.stat-label {
  margin-top: 0.35rem;
  font-weight: 800;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  font-size: 0.72rem;
  color: #666;
}
.stat-value {
  margin-top: 0.3rem;
  font-weight: 800;
  color: #333;
}

.pdp-cta {
  margin-top: 1rem;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 22px;
  padding: 1.1rem 1.25rem;
  box-shadow: 0 18px 44px rgba(0,0,0,0.06);
}

.pdp-add {
  width: 100%;
  border-radius: 14px;
  padding: 0.85rem 1rem;
  font-weight: 800;
  letter-spacing: 0.01em;
}

.pdp-cta-row {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 0.75rem;
  align-items: center;
}

.pdp-fav {
  width: 48px;
  height: 48px;
  border-radius: 14px;
  border: 1px solid #eee;
  background: #faf7f5;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.18s ease, border-color 0.18s ease, background 0.18s ease;
}

.pdp-fav:hover {
  transform: translateY(-1px);
  border-color: rgba(184, 92, 92, 0.55);
  background: #fff;
}

.pdp-fav-icon {
  width: 18px;
  height: 18px;
  stroke-width: 1.6;
  color: rgba(184, 92, 92, 0.9);
}

.pdp-trustline {
  margin-top: 0.75rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
  color: #666;
  font-size: 0.88rem;
}

.pdp-checklist {
  margin: 0.2rem 0 0;
  padding-left: 0;
  list-style: none;
  display: grid;
  gap: 0.5rem;
  color: #333;
}

.pdp-back {
  margin-top: 0.8rem;
}
.pdp-desc {
  margin: 0 0 1.1rem;
  line-height: 1.75;
  font-size: 1.02rem;
  color: #333;
}

.pdp-desc:last-child {
  margin-bottom: 0;
  font-style: italic;
  color: #777;
}

.pdp-desc strong {
  color: #b85c5c;
  font-weight: 700;
}

.qty-controls{
  display: inline-flex;
  align-items: center;
  gap: 0.45rem;
  margin-left: 0.6rem;
  flex: 0 0 auto;
}

.qty-btn{
  width: 30px;
  height: 30px;
  border-radius: 10px;
  border: 1px solid #eee;
  background: #faf7f5;
  cursor: pointer;
  font-weight: 900;
  line-height: 1;
}

.qty-value{
  min-width: 24px;
  text-align: center;
  font-weight: 800;
  color: #333;
}

.summary-meta{
  display: flex;
  align-items: center;
  gap: 0.6rem;
  flex-wrap: wrap;
}

.pdp-promo {
  margin-top: 1rem;
}

.pdp-promo-inner {
  background: #f4f4f4;
  border: 1px solid #d9d9d9;
  border-radius: 0;
  padding: 1.15rem 1rem;
  text-align: center;
}

.pdp-promo-title {
  font-size: 0.78rem;
  letter-spacing: 0.28em;
  font-weight: 800;
  color: #333;
  margin-bottom: 0.45rem;
  text-transform: uppercase;
}

.pdp-promo-text {
  color: #333;
  font-size: 0.95rem;
  line-height: 1.5;
}

.pdp-promo-code {
  font-weight: 800;
}


.checkout-form {
  display: grid;
  gap: 0.9rem;
  margin-top: 0.6rem;
}

.field {
  display: grid;
  gap: 0.35rem;
}

.field-label {
  font-weight: 700;
  font-size: 0.92rem;
  color: #333;
}

.field textarea {
  resize: vertical;
  min-height: 110px;
}

.field-error {
  color: #8a2d2d;
  font-size: 0.85rem;
  font-weight: 600;
}

@media (max-width: 520px) {
  .pdp-promo-inner {
    padding: 1rem 0.85rem;
  }

  .pdp-promo-text {
    font-size: 0.9rem;
  }
}

@media (max-width: 520px){
  .summary-item{
    grid-template-columns: 52px 1fr;
    row-gap: 0.4rem;
  }

  .summary-price{
    grid-column: 2;
    justify-self: start;
  }

  .summary-meta{
    gap: 0.5rem;
  }

  .qty-controls{
    margin-left: 0;
    gap: 0.35rem;
  }

  .qty-btn{
    width: 28px;
    height: 28px;
    border-radius: 9px;
  }

  .qty-value{
    min-width: 20px;
    font-size: 0.95rem;
  }
}

@media (max-width: 360px){
  .qty-btn{
    width: 26px;
    height: 26px;
    border-radius: 8px;
  }
  .qty-value{
    min-width: 18px;
    font-size: 0.9rem;
  }
}


@media (max-width: 980px) {
  .pdp-grid {
    grid-template-columns: 1fr;
  }
  .pdp-media img {
    height: 420px;
  }
  .pdp-stats {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 520px) {
  .pdp-shell {
    padding: 1rem 0.9rem 2.5rem;
  }
  .pdp-head,
  .pdp-story,
  .pdp-section,
  .pdp-cta {
    padding: 1rem;
    border-radius: 18px;
  }
  .pdp-media {
    border-radius: 18px;
    padding: 1rem;
  }
  .pdp-media img {
    height: 340px;
  }
  .pdp-stats {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .showcase-head {
    flex-direction: column;
    align-items: flex-start;
  }

 .showcase-rail {
    grid-auto-columns: 320px;
    justify-content: start;
  }

   .showcase-rail.single {
    grid-auto-columns: 340px;
    justify-content: center;
  }
}


@media (max-width: 768px) {
    .video-stage {
    height: 45vh;
    min-height: 360px;
    border-radius: 18px; 
    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    overflow: hidden;
    background: #111;
  }

  .video-player {
    border-top-left-radius: 18px;
    border-top-right-radius: 18px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    display: block;
  }

  .video-wrap {
    background: transparent;
  }
}

@media (max-width: 900px) {
  .checkout-layout {
    grid-template-columns: 1fr;
  }
  .checkout-right .summary-card {
    position: static;
  }
}

@media (max-width: 520px) {
  .pay-grid {
    grid-template-columns: 1fr;
  }
  .summary-item {
    grid-template-columns: 52px 1fr;
  }
  .summary-price {
    grid-column: 2;
  }
}

@media (max-width: 1280px) {
  .hero-content {
    margin-left: 0;
    padding-left: 1.5rem;
  }
}


 
@media (max-width: 900px) {
  .news-stats { grid-template-columns: repeat(2, minmax(0, 1fr)); }
}

@media (max-width: 520px) {
  .news-card-head { padding: 1rem; }
  .news-card-body { padding: 1rem; }
}


 
@media (max-width: 860px) {
  .founder-hero-inner {
    grid-template-columns: 1fr;
  }

  .founder-portrait {
    justify-self: start;
    width: min(320px, 100%);
  }
}


@media (max-width: 520px) {
  .founder-hero {
    padding: 3rem 1rem 2.2rem;
  }

  .letter-top {
    flex-direction: column;
    align-items: flex-start;
  }

  .letter-p {
    font-size: 1rem;
  }
}


@media (max-width: 900px) {
  .cart-layout {
    grid-template-columns: 1fr;
  }

  .cart-summary {
    position: static;
  }
}

@media (max-width: 520px) {
  .cart-item {
    grid-template-columns: 1fr;
  }

  .cart-thumb {
    width: 100%;
    height: 220px;
  }
}



@media (max-width: 992px) {
  .products-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .header {
    padding: 1rem;
  }

  .logo-img {
    height: 56px;
    max-width: 180px;
  }
  .desktop-nav,
  .desktop-account {
    display: none;
  }

  .hamburger {
    display: inline-flex;
    margin-left: auto;
    align-items: center;
    justify-content: center;
  }

  .footer {
    grid-template-columns: 1fr;
    text-align: center;
  }

    .newsletter-row {
    justify-content: center;
  }
  
}

@media (max-width: 768px) {
  .hero {
    height: clamp(380px, 70vh, 560px);
  }

  

  .hero-overlay {
    background: linear-gradient(
      to top,
      rgba(0,0,0,0.82) 0%,
      rgba(0,0,0,0.35) 55%,
      rgba(0,0,0,0.10) 100%
    );
  }

  .hero-content {
    padding: 2.2rem 1rem;
    justify-content: flex-end;
  }

  .hero-notes {
    max-width: 100%;
  }
}

@media (max-width: 420px) {
  .hero-content h1 {
    font-size: 2rem;
  }

  .dot {
    width: 14px;
    height: 14px;
  }
}

@media (max-width: 600px) {
  .products-grid {
    grid-template-columns: 1fr;
  }

  .perfume-details {
    grid-template-columns: 1fr;
  }

  .perfume-details img {
    max-width: 100%;
    aspect-ratio: 1 / 1;  
}
}

@media (max-width: 768px) {
  .hero-content {
    max-width: 100% !important;
    margin-left: 0 !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
  }
}

@media (max-width: 768px) {
  .landing {
    overflow-x: clip;
  }
}


@media (max-width: 768px) {
  .hero-bleed,
  .video-bleed {
    width: 100% !important;
    max-width: 100% !important;
    left: 0 !important;
    transform: none !important;
    margin: 0 !important;
  }
  .showcase-rail {
    display: flex !important;
    gap: 1rem !important;

    overflow-x: auto !important;
    overflow-y: hidden !important;

    -webkit-overflow-scrolling: touch;
    scroll-snap-type: x mandatory !important;
    overscroll-behavior-x: contain;

    padding: 0.25rem 1rem 0.75rem !important;
    scroll-padding-left: 1rem;
    scrollbar-width: none;
  }

  .showcase-rail::-webkit-scrollbar {
    display: none;
  }

  .showcase-item {
    flex: 0 0 65% !important;
    scroll-snap-align: start;
  }

  .perfume-card,
  .perfume-card img {
    width: 100%;
    max-width: 100%;
  }
}

@media (max-width: 420px) {
  .showcase-item {
    flex-basis: 65% !important;
  }
}

.hero-bleed,
.video-bleed {
  position: relative;
  width: 100%;
  margin: 0;
}

@media (min-width: 1024px) {
  .hero-bleed,
  .video-bleed {
    width: 100vw;
  }
    .hero.hero-static {
    height: 80vh;
  }
}

@media (max-width: 900px) {
  .collection-inner,
  .collection-card.reverse .collection-inner {
    grid-template-columns: 1fr !important;
  }

  .collection-card.reverse .collection-copy,
  .collection-card.reverse .collection-art {
    order: initial !important;
  }

  .collection-art {
    height: 180px;
    width: 100%;
  }

  .collection-art-text {
    width: 100%;
    text-align: center;
    word-break: break-word;
    overflow-wrap: anywhere;
  }
}


.hero-image,
.video-player {
  width: 100%;
  max-width: 100%;
  display: block;
}

html,
body,
#root,
.app,
.content,
.landing {
  max-width: 100%;
  overflow-x: hidden;
}
